@tailwind base;
@tailwind components;
@tailwind utilities;

/* google font */
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;800&family=VT323&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@300;600&family=Rubik+Vinyl&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Dai+Banna+SIL:wght@500&family=Oswald:wght@300;600&family=Rubik+Vinyl&display=swap");
/* layout  */
:root {
  --primary: #1aac83;
  --error: #e7195a;
}
body {
  background: #f1f1f1;
  margin: 0;
  font-family: "Poppins";
}

header {
  background: #fff;
}
header .container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header a {
  text-decoration: none;
}
.pages {
  max-width: 1700px;
  padding: 20px;
  margin: 0 auto;
}
button {
  background: var(--primary);
  border: 0;
  color: #fff;
  padding: 10px;
  font-family: "Poppins";
  border-radius: 4px;
  cursor: pointer;
}
